import React from 'react';
import { connect } from 'react-redux';
import { firestoreConnect } from 'react-redux-firebase';
import { compose } from 'redux';
import { Redirect } from 'react-router-dom';
import moment from '../../config/moment';


const ProjectDetail = (props) => {
	const { project, auth } = props;
	if (!auth.uid) return <Redirect to="/ingresar" />
	if ( project ){
		return (
			<div className="container section project-detail">
	    	<div className="card z-depth-0">
		    	<div className="card-content">
			    	<div className="row">
			    		<div className="col s1 push-s11">
			    		<button className="btn-floating btn-small waves-effect waves-dark white " title="Eliminar Proyecto">
			    		<i className="material-icons red-text">close</i>
			    		</button>
			    		</div>
		    		</div>
		    		<span className="card-title">{ project.title }</span>
		    		<p> { project.content } </p>

		    	</div>
		    	<div className="card-action  grey lighten-5 grey-text">
		    		<div>Creado Por : {project.authorFirstName} {project.authorLastName}</div>
		    		<div>{moment(project.createdAt.toDate()).calendar()}</div>
		    	</div>
	    	</div>
	    </div>
    )
	}else{
		return(
			<div className="conteiner center">
				<p>Cargando Projecto</p>
			</div>
  	)
	}				

}

const mapStateToProps = (state,ownProps) => {
	const id = ownProps.match.params.id;
	const projects = state.firestore.data.projects;
	const project = projects ? projects[id] : null 
	return{
		project:project,
		auth: state.firebase.auth
	}
}
export default compose(
	connect(mapStateToProps),
	firestoreConnect([
		{ collection:'projects'}
	])
)(ProjectDetail)