import React, {Component} from 'react';
import './mario.css';
import $ from 'jquery';
class BoxMario extends Component {

  componentDidMount = () => {
    var mario = $('#mario'),
      coin = $('#coin'),
      coinsound = $('#coinsound');
    
    mario.click(function(){
      coinsound.trigger('play');
      coin.bind("animationend webkitAnimationEnd oAnimationEnd MSAnimationEnd", function(){
        $(this).removeClass("active");
      }).addClass("active");
      
      return false;
    });
  }

  render(){
    return (
      <div className="size">
        <button id="mario">
          <img src="http://bosssss.com/mario/coin.gif" id="coin" />
          <audio id="coinsound">
            <source src="http://bosssss.com/mario/coinsound.mp3"></source>
          </audio>
        </button>
      </div>
    );
  }
}

export default BoxMario;